@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
*{
    margin: 0;
}
.app{
    background-color: #f1f2f5;
    height: 100vh;
}
.app__body {
    display: flex;
}
 
 
 

.feed{
    flex: 1 1;
    padding: 30px 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.messageSender {
    display: flex;
    margin: 1.9rem;
    flex-direction: column;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0 5px 7px -7px rgba(0, 0,0, 0.75);
    width: 100%;
}

.messageSender__top {
    display: flex;
    border-bottom: 1px solid #eff2f5;
    padding: 15px;
}

.messageSender > bottom {
    flex: 1 1;
    display: flex;
}
.messageSender__top > form > input {
    outline: 0;
    border: none;
    padding: 5px 20px;
    margin: 0 10px;
    border-radius: 999px;
    background-color: #eff2f5;
}
.messageSender__top > form > button {
    display: none;
}

.messageSender__input {
    flex: 1 1;
}

.messageSender__bottom {
    display: flex;
    justify-content: space-evenly;
}

.messageSender__option {
    padding: 20px;
    display: flex;
    align-items: center;
    color: gray;
    margin: 5px;
} 
.messageSender__option > h3 {
    font-size: medium;
    margin-left: 10px;
    cursor: pointer;
}

.messageSender__option:hover {
    background-color: #effef5;
    border-radius: 20px;
}
.story {
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 120px;
    height: 200px;
    box-shadow: 0 5px 17px -7px rgba(0, 0, 0, 0.75);
    border-radius: 10px;
    margin-right: 10px;
    transition: transform 100ms ease-in;
    cursor: pointer;

}

.story:hover {
    transform: scale(1.07);
}
.story__avatar {
    margin: 10px;
    border: 5px solid #2e81f4;
}

.story > h4 {
    position: absolute;
    bottom: 20px;
    left: 20px;
    color: black;
}
.storyReel {
    display: flex;
}

.storyReel__story {
    margin-left: 10px;
}
.post {
    width: 100%;
    margin-top: 1rem;
    border-radius: 1rem;
    background-color: white;
    box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
}
.post__top {
    display: flex;
    position: relative;
    align-items: center;
    padding: 1rem;
}

 
.post__avatar {
    margin-right: 10px;
}

.post__bottom {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 15px 20px;
}

.post__topInfo > h3 {
    font-size: medium;
}
.post__topInfo  > p{
    font-size: small;
    color: gray;
}

.post__image > img {
    width: 100%;
}

.post__options {
    padding-top: 10px;
    border-top: 1px solid lightgray;
    display: flex;
    justify-content: space-evenly;
    padding: 1rem;
    cursor: pointer;
    font-size: medium;
    color: gray;
}

.post__option{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    flex: 1 1;

}

.post__option > p {
    margin-left: 10px;
}
.post__option:hover {
    background-color: #eff2ef;
    border-radius: 10px;

}
.Header {
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    background-color: white;
    z-index: 100;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0,0, 0, 75);
}

.header__left {
    display: flex;
    justify-content: space-evenly;
}

.header__input {
    display: flex;
    align-items: center;
    background-color: #eff2f5;
    padding: 10px;
    margin-left: 10px;
    border-radius: 33px;
}

.header__input > input {
    border: none;
    background-color: transparent;
    outline-width: 0;
}

.header__left > img {
    height: 2.3rem;
}

 .header__middle {
     display: flex;
     flex: 1 1;
     justify-content: center;
 }

 .header__option > .MuiSvgIcon-root {
     color: gray;
 }

 .header__option:hover > .MuiSvgIcon-root {
    color: #2e81f4;
}

.header__option--active > .MuiSvgIcon-root {
    color: #2e81f4;
}

.header__option--active {
    border-bottom: 4px solid #2e81f4;
}

.header__option {
    display: flex;
    align-items: center;
    padding: 0 30px;
    cursor: pointer;
}

.header__option:hover {
    background-color: #eff2f5;
    border-radius: 10px;
    align-items: center;
    padding: 0 3px;
    border-bottom: none;
}
.header__info {
    display: flex;
    align-items: center;
}

.header__info > h4 {
    margin-left: 10px;
}

.header__right {
    display: flex;
}
