.post {
    width: 100%;
    margin-top: 1rem;
    border-radius: 1rem;
    background-color: white;
    box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
}
.post__top {
    display: flex;
    position: relative;
    align-items: center;
    padding: 1rem;
}

 
.post__avatar {
    margin-right: 10px;
}

.post__bottom {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 15px 20px;
}

.post__topInfo > h3 {
    font-size: medium;
}
.post__topInfo  > p{
    font-size: small;
    color: gray;
}

.post__image > img {
    width: 100%;
}

.post__options {
    padding-top: 10px;
    border-top: 1px solid lightgray;
    display: flex;
    justify-content: space-evenly;
    padding: 1rem;
    cursor: pointer;
    font-size: medium;
    color: gray;
}

.post__option{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    flex: 1;

}

.post__option > p {
    margin-left: 10px;
}
.post__option:hover {
    background-color: #eff2ef;
    border-radius: 10px;

}